import { TIMEOUT_SECONDS } from './config';

const timeout = function (s) {
  return new Promise(function (_, reject) {
    setTimeout(function () {
      reject(new Error(`Request took too long! Timeout after ${s} second`));
    }, s * 1000);
  });
};

export const getJSON = async function (url) {
  // srekli yapacamz ilemleri helpers.js in iinde toplayp export ediyoruz. Burada async fonksiyonun iinde async fonksiyonu altrm oluyoruz. Ve ayrca parent async fonksiyonun iindeki erroru dzenleyebilmemiz iin buradaki try-catch bloundaki erroru throwlamamz gerekiyor.
  try {
    const res = await Promise.race([
      fetch(
        url // id hash'li url fetch edilir.
      ),
      timeout(TIMEOUT_SECONDS),
    ]);
    const data = await res.json(); // response json'a dntrlr.

    if (!res.ok) throw new Error(`${data.message} (${res.status})`); // eer fetch etme srasnda hata oluursa yeni error oluturulur.
    // console.log(data);
    return data;
  } catch (error) {
    throw error;
  }
};
